const TRACTATE_IDS = {
  "Berakhot": 1,
  "Shabbat": 2,
  "Eruvin": 3,
  "Pesachim": 4,
  "Shekalim": 5,
  "Yoma": 6,
  "Sukkah": 7,
  "Beitzah": 8,
  "Rosh Hashanah": 9,
  "Taanit": 10,
  "Megillah": 11,
  "Moed Katan": 12,
  "Chagigah": 13,
  "Yevamot": 14,
  "Ketubot": 15,
  "Nedarim": 16,
  "Nazir": 17,
  "Sotah": 18,
  "Gittin": 19,
  "Kiddushin": 20,
  "Bava Kamma": 21,
  "Bava Metzia": 22,
  "Bava Batra": 23,
  "Sanhedrin": 24,
  "Makkot": 25,
  "Shevuot": 26,
  "Avodah Zarah": 27,
  "Horayot": 28,
  "Zevachim": 29,
  "Menachot": 30,
  "Chullin": 31,
  "Bekhorot": 32,
  "Arakhin": 33,
  "Temurah": 34,
  "Keritot": 35,
  "Meilah": 36,
  "Niddah": 37
};
class HebrewBooksAPI {
  async fetchPage(tractate, daf, options = {}) {
    try {
      const mesechtaId = TRACTATE_IDS[tractate];
      if (!mesechtaId) {
        throw new Error(`Unknown tractate: ${tractate}`);
      }
      const dafNum = parseInt(daf.replace(/[ab]/, ""));
      const amud = daf.includes("b") ? "b" : "a";
      const dafParam = amud === "a" ? dafNum.toString() : `${dafNum}b`;
      const params = new URLSearchParams({
        mesechta: mesechtaId,
        daf: dafParam,
        ...options
        // Include any additional options like br=true
      });
      const endpoint = `https://daf-supplier.402.workers.dev?${params.toString()}`;
      console.log("Fetching from daf-supplier:", endpoint);
      const response = await fetch(endpoint);
      if (!response.ok) {
        throw new Error("Failed to fetch HebrewBooks data");
      }
      const data = await response.json();
      console.log("Response from daf-supplier:", data);
      const mappedData = {
        tractate: data.tractate || tractate,
        daf: daf.replace(/[ab]$/, ""),
        // Use our input, not data.dafDisplay
        amud: daf.includes("b") ? "b" : "a",
        // Use our input, not data.amud
        mainText: data.mainText || "",
        rashi: data.rashi || "",
        tosafot: data.tosafot || "",
        otherCommentaries: data.otherCommentaries,
        timestamp: data.timestamp || Date.now()
      };
      return mappedData;
    } catch (error) {
      console.error("Error fetching HebrewBooks page:", error);
      return null;
    }
  }
}
const hebrewBooksAPI = new HebrewBooksAPI();
export {
  TRACTATE_IDS as T,
  hebrewBooksAPI as h
};
